import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {uniPopup},
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {

	},
	methods: {
		toReLaunch(url){
			uni.reLaunch({
				"url":url
			})
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		toTab(url){
			uni.switchTab({
				url:"/pages/address/address"
			})
		}
	}
}
